
/*---########  Navbar CSS   ########---*/
body{
  background-color: var(--primary-bg-color);
}
#navbar a{
  text-decoration: none;
  color: white;
  transition: all 0.7s;
}
#navbar a:hover{
  
  color: var(--secondary-text-color);
}

#navbar{
  display: flex;
  justify-content: space-between;
  /* margin:0; */
  position: sticky;
  top: 0;
  width:100%;
  z-index: 9999;
  padding: 5 15px;
   background-color:var(--tertiary-bg-color);
  border-bottom: 1px solid var(--secondary-text-color);
}
.nav-logo{
  display: block;
   background-color:var(--tertiary-bg-color);
   padding-left:10px;

}

.avtar{
  display: inline-block;
  width:50px;
  
  height:50px;
  
}
.nav-left{
 display: flex;
 
 background-color:var(--tertiary-bg-color); 
}
.search{
  display: flex;
  width: 30vw;
  padding-top:5px;
  
  background-color:var(--tertiary-bg-color); 
}
.search-icon{
  height: 5px;
  padding: 18px 180px;
  
}

.nav-right{
  display: flex;
  
  
}
.nav-items{
  display: flex;
  
  list-style-type: none;
 
  padding: 0 10px;
}
.nav-items>li{
  color:white;
  padding: 0 0.5rem;
}




/* Signup CSS */
.background-img{
  width:100vw;
  animation:fadePage;
  animation-duration: 1s;
  /* height:100vh; */
  
  background-image: url("./Images/background.jpg");
  background-repeat: no-repeat;
  background-size:cover;
  /* filter: brightness(15%); */
  
}
@keyframes fadePage{
  0%{
    position: relative;
    left:-100%;
    opacity: 0;
  }
  100%{
    position: relative;
    left: 0%;
    opacity: 1;
  }
}
.input-container{
  display: flex;
    flex-direction: row;
    border-bottom: 1px solid var(--secondary-text-color);
    justify-content: center;
    align-items: center;
    margin: 5px 0;
}
.top-container{
  display: flex;
  justify-content: center;
  width: 100%;
  
  
}
.signup-container, .login-container{
 display: flex;
 flex:0.7;
  justify-content: center;
  margin:25px 0;
  opacity: 10;
 
}

.signup-form, .login-form{
  display: flex;
  flex-direction: column;
  padding:15px 0;
  width:50%;
  /* height:80vh; */
  background-color: var(--secondary-bg-color);
  
  border-radius: 20px;
    text-align: center;
}

.signup-form>h2, .login-form>h2{
  font-size: 2.5rem;
  color: var(--primary-color);
  padding:40px 0 20px 0;
  margin:0;
}
.signup-form>form, .login-form>form{
  display: flex;
  flex-direction: column;
  padding:0 30px ;
}
input[type="text"], input[type="email"], input[type="number"],input[type="password"]{
  padding: 10px ;
  text-decoration: none;
  border: none;
  background-color: transparent;
  outline: none;
  color:var(--tertiary-text-color);
  width: 100%;
  
}
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px var(--secondary-bg-color) inset !important;
    -webkit-text-fill-color: wheat;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
hr{
  border: 1px solid #2B2828;
  width:100%;
   
  
}

.selectElement{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color:var(--secondary-text-color);
  
  
}
.select-college{
   display: flex;
   width: 100%;
   padding:15px 0 2px 0;
   
  }
  .clg{
  /* margin-left: 15px; */
  width: 100%;
  padding: 0 9px;

}
.course-sem{
  display:flex;
  flex-direction: row;
  width: 100%;
}
.select-courses{
   display: flex;
   width: 100%;
   padding:15px 0 2px 0;
   
}
.select-semester{
   display: flex;
   width: 100%;
   padding:15px 0 2px 0;
   margin-left: 5px;
   
}
select{
  border:none;
  outline: none;
  width:90%;
  background-color: transparent;
  color:var(--secondary-text-color);
}

.checkbox{
  display: flex;
  color:var(--secondary-text-color);
  padding:8px 0;
  font-size:12px;
}
.checkbox-icon{
  color:var(--primary-color);
  padding:0 2px;
  font-size: 15px;
}

.signup-desc, .login-desc{
  padding:50px;
  display: flex;
  flex:0.3;
  color:white;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  
}

.register-btn,.login-btn,.get-started,.sign-in,.sign-up{
  cursor: pointer;
  width:50%;
  border-radius: 5px;
  
  padding:13px 20px;
  display: flex;
  background-color: var(--primary-color);
  color:white;
  border: none;
  justify-content: center;
  font-size: 15px;
}
form>.register-button, form>.login-button{
  display:flex;
  justify-content: center;
  align-items: center;

}
.signup-footer,.login-footer{
  color:var(--tertiary-text-color);
}
.signup-footer>a,.login-footer>a{
  color:var(--primary-color);
  text-decoration: none;
  
}

.signup-desc>h2{
  width:50px;
  font-size:40px;
}
.signup-desc>p{
  width:50px;
  font-size:30px;
}
.button{
  display: flex;
  justify-content: center;
  width:80%;
  gap:20px;
 
}
.button>.sign-in, .button>.sign-up{
     background-color: transparent;
     border:2px solid var(--primary-color);
     transition: all 1s;
}
.button>.sign-in:hover, .button>.sign-up:hover{
     background-color: var(--primary-color);
     color:white;
}
.button>.get-started{
  /* margin-right:20px; */
  border:2px solid var(--primary-color);
}

